import React, { Fragment, useEffect, useState } from 'react'
import './Checkout.scss'
import { CloseOutlined, CheckOutlined } from '@ant-design/icons';
import styled from 'styled-components';
import { useDispatch, useSelector } from 'react-redux';
import { connection } from '../..';
import { datGheAction, datVeAction, layChiTietPhongVeAction } from '../../redux/actions/QuanLyDatVeAction';
import { DAT_GHE } from '../../redux/actions/types/QuanLyDatVeType';
import _ from 'lodash';
import { ThongTinDatVe } from '../../_core/models/ThongTinDatVe';
import { history } from '../../App';
import { Modal, Button } from 'antd';
import { layThongTinTaiKhoanAction } from '../../redux/actions/QuanLyNguoiDungAction';
import moment from 'moment';

export default function CheckoutIpad(props) {

    //modal antd
    const [isModalVisible, setIsModalVisible] = useState(false);

    const showModal = () => {
        setIsModalVisible(true);
    };

    const handleOk = () => {
        setIsModalVisible(false);
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };


    const { userLogin, thongTinNguoiDung } = useSelector(state => state.QuanLyNguoiDungReducer);

    const { chiTietPhongVe, danhSachGheDangDat, danhSachGheKhachDat } = useSelector(state => state.QuanLyDatVeReducer);


    const dispatch = useDispatch();

    useEffect(() => {

        //gi hm to ra 1 async function
        const action = layChiTietPhongVeAction(props.match.params.id)
        dispatch(action);

        //C 1 client no thc hin vic t v thnh cng mnh s load li danh sch phng v ca lch chiu 
        connection.on('datVeThanhCong', () => {
            dispatch(action)
        });

        //va vo trang load tt c gh ca cc ngi khc ang t

        connection.invoke("loadDanhSachGhe", props.match.params.id);

        //Load danh sch gh ang t t server v
        connection.on("loadDanhSachGheDaDat", (dsGheKhachDat) => {
            console.log('danhSachGheKhachDat', dsGheKhachDat);

            //b1: loi mnh ra khi danh sch

            dsGheKhachDat = dsGheKhachDat.filter(item => item.taiKhoan !== userLogin.taiKhoan);

            //b2: gp danh sch gh khch t  tt c user thnh 1 mng chung

            let arrGheKhachDat = dsGheKhachDat.reduce((result, item, index) => {
                let arrGhe = JSON.parse(item.danhSachGhe);

                return [...result, ...arrGhe];
            }, [])
            //a d liu gh khch t cp nht redux
            arrGheKhachDat = _.uniqBy(arrGheKhachDat, 'maGhe');

            //a d liu v redux
            dispatch({
                type: DAT_GHE,
                payload: arrGheKhachDat
            })
        })

        //ci t s kin khi reload trang

        window.addEventListener("beforeunload", clearGhe);


        return () => {
            clearGhe();
            window.removeEventListener("beforeunload", clearGhe);
        }

    }, [])

    const clearGhe = (event) => {

        connection.invoke('huyDat', userLogin.taiKhoan, props.match.params.id)
    }

    console.log({ chiTietPhongVe });

    console.log('danhSachGheDangDat', danhSachGheDangDat)

    const { thongTinPhim, danhSachGhe } = chiTietPhongVe;

    const renderGhe = () => {
        return danhSachGhe.map((ghe, index) => {

            let classGheVip = ghe.loaiGhe === 'Vip' ? 'gheVip' : '';
            let classGheDaDat = ghe.daDat === true ? 'gheDaDat' : '';
            let classGheDangDat = '';

            let indexGheDangDat = danhSachGheDangDat.findIndex(gheDangDat => gheDangDat.maGhe === ghe.maGhe);

            if (indexGheDangDat !== -1) {
                classGheDangDat = 'gheDangDat';
            }

            let classGheDaDuocDat = '';
            if (userLogin.taiKhoan === ghe.taiKhoanNguoiDat) {
                classGheDaDuocDat = 'gheDaDuocDat';
            }

            let classGheKhachDat = '';
            let indexGheKD = danhSachGheKhachDat.findIndex(gheKD => gheKD.maGhe === ghe.maGhe)
            if (indexGheKD !== -1) {
                classGheKhachDat = 'gheKhachDat';
            }

            return <Fragment key={index}>
                <button type="button" className={`ghe ${classGheVip}  ${classGheDaDat} ${classGheDangDat} ${classGheDaDuocDat} ${classGheKhachDat} text-center`} disabled={ghe.daDat || classGheKhachDat !== ''} onClick={() => {
                    dispatch(datGheAction(ghe, props.match.params.id))
                }} key={index} style={{ fontSize: 11 }}>

                    {/* {ghe.daDat ? classGheDaDuocDat !== '' ? <CheckOutlined style={{ fontWeight: 'bold' }} /> : <CloseOutlined style={{ fontWeight: 'bold' }} /> : classGheKhachDat !== '' ? ghe.stt : ghe.stt} */}
                    {/* {classGheDangDat !== '' ? ghe.stt : ''} */}

                    {classGheDangDat !== '' ? ghe.stt : <Fragment><span className="opacity-0">ghe</span></Fragment>}

                </button>

                {(index + 1) % 16 === 0 ? <br /> : ''}
            </Fragment>
        })
    }

    let classBtnDatVe = '';

    if (danhSachGheDangDat.length === 0) {
        classBtnDatVe = 'disabled';
    }


    const renderTicketItem = () => {
        return thongTinNguoiDung.thongTinDatVe?.map((ticket, index) => {

            const danhSachGhe = _.first(ticket.danhSachGhe);
            return <div className="p-2 w-full" key={index}>
                <div className="h-full flex items-center border-gray-200 border p-4 rounded-lg">
                    <img alt="team" className="w-16 h-16 bg-gray-100 object-cover object-center flex-shrink-0 rounded-full mr-4" src={ticket.hinhAnh} />
                    <div className="flex-grow">
                        <h2 className="text-gray-900 title-font font-medium">{ticket.tenPhim}</h2>
                        <p className="text-gray-500">{moment(ticket.ngayDat).format('hh:mm A')} - Ngy Chiu {moment(ticket.ngayDat).format('DD-MM-YYYY')} </p>
                        <p className="text-gray-500">Thi Lng: {ticket.thoiLuongPhim} pht</p>
                        <p className="text-gray-500">a im: {danhSachGhe.tenHeThongRap}</p>
                        <p className="text-gray-500">Tn Rp: {danhSachGhe.tenCumRap} - Gh {ticket.danhSachGhe.map((ghe, index) => {
                            return <span key={index}> {ghe.tenGhe} </span>
                        })}</p>
                    </div>
                </div>
            </div>
        }).reverse();
    }


    return (
        <div>
            {/* Checkout header */}
            <div className="checkout-header">
                <div className="left">
                    <div className="backButton">
                        <CloseOutlined className="text-2xl" onClick={() => {
                            history.push(`/detail/${props.match.params.id}`)
                        }} />
                    </div>
                </div>
                <div className="center">
                    <span className="steptitle">Chn Gh</span>
                </div>
                <div className="right">
                    <div className="account">
                        <UserProfile className="ml-5 rounded-full bg-blue-100" onClick={() => {
                            history.push('/profile')
                        }}>{userLogin.taiKhoan.substr(0, 1)}</UserProfile>
                    </div>
                </div>
            </div>
            {/* Checkout body */}

            <div className="seatCheckout">
                <div className="">
                    <div className="top-content flex justify-between w-full">
                        <div className="left-title flex items-center">
                            {/* <div className="logo-cinema">
                                <img className="logo" src="https://s3img.vcdn.vn/123phim/2018/09/f32670fd0eb083c9c4c804f0f3a252ed.png" alt="logo-cinema" />
                            </div> */}
                            <div className="content-cinema">
                                <p className="address">
                                    <span className="p-cinema">{thongTinPhim.tenPhim}</span>
                                </p>
                                <p className="address">
                                    <span className="p-cinema">{thongTinPhim.tenCumRap}</span>
                                    <span className="cinema-name"></span>
                                </p>
                                <p className="hour">
                                    {thongTinPhim.ngayChieu} - {thongTinPhim.gioChieu} -
                                    {thongTinPhim.tenRap}
                                </p>
                            </div>
                        </div>
                        <div className="right-title">
                            <p>Ti Khon: {userLogin.taiKhoan.length > 15 ? userLogin.taiKhoan.slice(0, 15) + '...' : userLogin.taiKhoan}</p>
                            <p>S T: {userLogin.soDT}</p>
                        </div>
                    </div>
                    <div className="seat-map">
                        <div className="panzoom">
                            <div className="screen flex justify-center">
                                <img src="../../Images/screen.png" alt="screen" style={{ width: '90%' }} />
                            </div>

                            <div className="list-seat">
                                {renderGhe()}
                            </div>

                            <div className="mt-10 flex justify-center px-10">
                                <table className="divide-y divide-gray-200 w-full text-center" >
                                    <thead className="bg-gray-50 p-5">
                                        <tr>
                                            <th>Gh cha t</th>
                                            <th>Gh ang t</th>
                                            <th>Gh  c t</th>
                                            <th>Gh vip</th>
                                            <th>Gh bn  t</th>
                                            <th>Gh ngi khc ang t</th>
                                        </tr>
                                    </thead>
                                    <tbody className="bg-white divide-y divide-gray-200">
                                        <tr>
                                            <td><button className="ghe text-center"></button></td>
                                            <td><button className="ghe gheDangDat text-center"></button></td>
                                            <td><button className="ghe gheDaDat text-center"></button></td>
                                            <td><button className="ghe gheVip text-center"></button></td>
                                            <td><button className="ghe gheDaDuocDat text-center"></button></td>
                                            <td><button className="ghe gheKhachDat text-center"></button></td>
                                        </tr>
                                    </tbody>
                                </table>

                            </div>

                        </div>
                    </div>
                </div>
            </div>

            <div className="grid grid-cols-2 checkout-footer">
                <div className="total">
                    <p className="seat-chosen">
                        {_.sortBy(danhSachGheDangDat, ['maGhe']).map((gheDangDat, index) => (
                            <span key={index}>{gheDangDat.tenGhe} - </span>
                        ))}
                        {
                            danhSachGheDangDat.reduce((tongTien, gheDangDat, index) => {
                                return tongTien += gheDangDat.giaVe
                            }, 0).toLocaleString()
                        }</p>
                </div>
                <div className={`${classBtnDatVe} continue-button flex items-center justify-center`} onClick={async () => {
                    const thongTinDatVe = new ThongTinDatVe();
                    thongTinDatVe.maLichChieu = props.match.params.id;
                    thongTinDatVe.danhSachVe = danhSachGheDangDat;

                    console.log(thongTinDatVe);
                    await dispatch(datVeAction(thongTinDatVe))
                    await dispatch(layThongTinTaiKhoanAction());

                    showModal();

                }}>
                    t V
                </div>

                <Modal title="Lch S t V" visible={isModalVisible} footer={null} onOk={handleOk} onCancel={handleCancel}>
                    <div className="scroll__bar__custom w-full" style={{ height: '400px', overflow: 'auto' }}>
                        {renderTicketItem()}
                    </div>
                </Modal>
            </div>

        </div>
    )
}


const UserProfile = styled.div`
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
`
