import React, { useState } from 'react'
import { useFormik } from 'formik';
import { capNhatNguoiDungAction, dangKyAction, dangNhapAction, themNguoiDungAction } from '../../redux/actions/QuanLyNguoiDungAction';
import { useDispatch, useSelector } from 'react-redux';
import "./Login.scss";
import * as Yup from 'yup';
import { GROUPID } from '../../Util/setting';
import bg from '../../assets/footer-bg.jpg';

export default function Login(props) {

    const dispatch = useDispatch()

    const [slideUp, setSlideUp] = useState(true);

    const [loginFail, setLoginFail] = useState(false);

    const { userLogin } = useSelector((state) => state.QuanLyNguoiDungReducer)


    const { errors } = useSelector((state) => state.QuanLyNguoiDungReducer);
    console.log('errorsScreen', errors)

    console.log('userLogin', userLogin)

    const formik = useFormik({
        initialValues: {
            taiKhoan: '',
            matKhau: '',
        },
        onSubmit: values => {

            const action = dangNhapAction(values);

            dispatch(action);


            console.log('values', values);
        },
    });


    const formikRegister = useFormik({
        initialValues: {
            taiKhoan: "",
            matKhau: "",
            email: "",
            soDt: "",
            maNhom: GROUPID,
            maLoaiNguoiDung: 'KhachHang',
            hoTen: ""
        },

        //.matches('^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\ [[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$', 'khng hp l!!!')
        validationSchema: Yup.object().shape({
            hoTen: Yup.string().required('Khng c b trng!!!').matches('^[A-Z a-z]+$', 'Vui lng nhp h tn ph hp!!!'),
            taiKhoan: Yup.string().required('khng c b trng!!!'),
            email: Yup.string().required('khng c b trng!!!').email('khng hp l!!!'),
            matKhau: Yup.string().required('khng c b trng!!!').min(6, 'phi t 6-32 k t!!!').max(32, 'phi t 6 - 32 k t!!!'),
            soDt: Yup.string().required('khng c b trng!!!').min(10, 'Vui lng nhp s T hp l!!!').max(11, 'Vui lng nhp s T hp l!!!').matches('^[0-9]+$', 'Vui lng nhp s in thoi!!!'),
        }),
        onSubmit: async values => {
            values.maNhom = GROUPID;
            const action = dangKyAction(values);
            await dispatch(action);

            console.log('values', values);
        },
    })


    let classLogin = 'slide-up';
    let classRegister = '';


    if (slideUp) {
        //login
        classRegister = 'slide-up';
        classLogin = '';
    } else {
        //register
        classRegister = '';
        classLogin = 'slide-up';
    }

    return (

        <div className="flex justify-center items-center bg-login h-screen" style={{ backgroundImage: `url(${bg})` }}>
            <div className="form-structure">
                <div className={`signup ${classRegister}`}>
                    <h2 className="form-title" id="signup" onClick={(e) => {
                        setSlideUp(false);
                    }}><span>hoc</span>ng K</h2>
                    <form onSubmit={formikRegister.handleSubmit}>
                        <div className="form-holder shadow-md">
                            <div className="user-box">
                                <input type="text" required name="hoTen" onChange={formikRegister.handleChange} onBlur={formikRegister.handleBlur} />
                                <label>H Tn{formikRegister.errors.hoTen && formikRegister.touched.hoTen ? (<span style={{ color: 'red' }}><span className="text-black">:</span> {formikRegister.errors.hoTen} </span>) : null}</label>

                            </div>
                            <div className="user-box">
                                <input type="text" name="taiKhoan" onChange={formikRegister.handleChange} onBlur={formikRegister.handleBlur} />
                                <label>Ti Khon{formikRegister.errors.taiKhoan && formikRegister.touched.taiKhoan ? (<span style={{ color: 'red' }}><span className="text-black">:</span> {formikRegister.errors.taiKhoan} </span>) : null}</label>
                            </div>
                            <div className="user-box">
                                <input type="email" name="email" required onChange={formikRegister.handleChange} onBlur={formikRegister.handleBlur} />
                                <label>Email{formikRegister.errors.email && formikRegister.touched.email ? (<span style={{ color: 'red' }}><span className="text-black">:</span> {formikRegister.errors.email} </span>) : null}</label>
                            </div>
                            <div className="user-box">
                                <input type="password" name="matKhau" onChange={formikRegister.handleChange} onBlur={formikRegister.handleBlur} />
                                <label>Mt Khu{formikRegister.errors.matKhau && formikRegister.touched.matKhau ? (<span style={{ color: 'red' }}><span className="text-black">:</span> {formikRegister.errors.matKhau} </span>) : null}</label>
                            </div>
                            <div className="user-box">
                                <input type="text" name="soDt" onChange={formikRegister.handleChange} onBlur={formikRegister.handleBlur} />
                                <label>S in Thoi{formikRegister.errors.soDt && formikRegister.touched.soDt ? (<span style={{ color: 'red' }}><span className="text-black">:</span> {formikRegister.errors.soDt} </span>) : null}</label>
                            </div>
                        </div>
                        <button className="submit-btn">ng K</button>
                    </form>
                </div>
                <div className={`login ${classLogin}`}>
                    <form onSubmit={formik.handleSubmit}>
                        <div className="center">
                            <h2 className="form-title" id="login" onClick={(e) => {
                                setSlideUp(true);
                            }}><span>hoc</span>ng Nhp</h2>
                            <div className="form-holder shadow-md" style={{ borderRadius: '0px !important', padding: 10 }}>
                                <div className="user-box" >
                                    <input type="text" name="taiKhoan" onChange={formik.handleChange} />
                                    <label>Ti Khon</label>
                                </div>
                                <div className="user-box mt-5">
                                    <input type="password" name="matKhau" onChange={formik.handleChange} />
                                    <label>Mt Khu</label>
                                </div>
                            </div>
                            <button className="submit-btn" onClick={() => {
                                if (errors) {
                                    setLoginFail(true);
                                }
                            }}>ng Nhp</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}












