import React, { useState } from 'react';
import {
  Form,
  Input,
  Radio,
  Button,
  Select,
} from 'antd';
import { useFormik } from 'formik';
import { useDispatch } from "react-redux";
import { themPhimUploadHinhAction } from '../../../../redux/actions/QuanLyPhimAction';
import { GROUPID } from '../../../../Util/setting';
import { themNguoiDungAction } from '../../../../redux/actions/QuanLyNguoiDungAction';
import { Row, Col, Divider  } from 'antd';
import noImage from '../../../../Images/NoImage.png';


const AddNew = () => {
  const [componentSize, setComponentSize] = useState('default');
   const dispatch = useDispatch();

  const formik = useFormik({
      initialValues: {
        taiKhoan: '',
        matKhau: '',
        email: '',
        soDt: '',
        maNhom: GROUPID,
        maLoaiNguoiDung: '',
        hoTen: '',
      },
      onSubmit: async (values, { resetForm }) =>{
        console.log('values', values)
        values.maNhom = GROUPID;
        await dispatch(themNguoiDungAction(values));
        resetForm();
      }
  })
  

  const handleChangeLoaiUser = async (values) => {
    formik.setFieldValue('maLoaiNguoiDung',values)
  }
  const onFormLayoutChange = ({ size }) => {
    setComponentSize(size);
  };
  const convertSelectUser = () => {
    return [{label: 'Khch hng', value: "KhachHang"}, {label: 'Qun Tr', value: "QuanTri"}]
}
  return (
    <>
      <Form
      onSubmitCapture= {formik.handleSubmit}
        labelCol={{
          span: 4,
        }}
        wrapperCol={{
          span: 14,
        }}
        layout="horizontal"
        initialValues={{
          size: componentSize,
        }}
        onValuesChange={onFormLayoutChange}
        size={componentSize}
      >
      <Row>
        <Col xs={{span: 24}} xl={{span: 24}}>
          <Divider orientation="left">Thm mi ngi dng</Divider>
        </Col>
      </Row>
      <Row>
      <Col xs={{span: 24}} xl={{span: 12}} >
      <Form.Item label="Form Size" name="size">
          <Radio.Group>
            <Radio.Button value="small">Small</Radio.Button>
            <Radio.Button value="default">Default</Radio.Button>
            <Radio.Button value="large">Large</Radio.Button>
          </Radio.Group>
        </Form.Item>
        <Form.Item label="Ti Khon" onChange={formik.handleChange}  rules={[{ required: true, message: 'Hy nhp ti khon!' }]}>
          <Input name="taiKhoan"  value={formik.values.taiKhoan}/>
        </Form.Item>
        <Form.Item label="Mt Khu"  onChange={formik.handleChange}>
          <Input.Password name="matKhau"  value={formik.values.matKhau}/>
        </Form.Item>
        <Form.Item label="H Tn"  onChange={formik.handleChange}>
          <Input name="hoTen"  value={formik.values.hoTen}/>
        </Form.Item>
        <Form.Item label="S T"  onChange={formik.handleChange}>
          <Input name="soDt"  value={formik.values.soDt}/>
        </Form.Item>
        <Form.Item label="Email"  onChange={formik.handleChange}>
          <Input name="email"  value={formik.values.email}/>
        </Form.Item>
        <Form.Item label="Loi User"  onChange={formik.handleChange}>
          <Select options={convertSelectUser()} onChange={handleChangeLoaiUser} placeholder="Chn loi ngi dng!" />
        </Form.Item>

        <Form.Item label="Tc v">
          <Button type="primary" danger htmlType="submit">
          Thm ngi dng
          </Button>
        </Form.Item>
        </Col>
      </Row>
      </Form>
    </>
  );
};

export default AddNew;
